.All {
  margin: 0;
  padding: 0;
  overflow: hidden;
  margin-top: -5vw;
}

.BkgSquiggles {
  display: relative;
  width: 100vw;
  height: fit-content;
  object-fit: cover;
  background-repeat: none;
  position: static;
  z-index: 0;
}

.ProjectWrapper {
  display: relative;
  position: absolute;
  width: 100vw;
  height: 50vw;
  object-fit: cover;
  background-repeat: none;
  position: static;
  z-index: 0;
  margin-top: -52vw;
}

.Square {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  text-align: start;
  width: 24vw;
  height: 35.5vw;
  background-image: url(./../../Assets/BkgPaper.webp);
  object-fit: cover;
  border: .3vw solid #C2EDF4;
  border-radius: 3vw;
  position: relative;
  z-index: 3;
  margin-left: 4.7vw;
}

.WrapperCol {
  display: flex;
  flex-direction: column;
  width: 24vw;
}

.WrapperRow {
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: 24vw;
}

.Wrap {
  display: flex;
  flex-direction: row;
  width: 24vw;
  justify-content: center;
  margin-left: 4.7vw;
  
}

.Button {
  display: flex;
  position: relative;
  width: fit-content;
  height: fit-content;
  padding: 1vw;
  font-family: 'BIZ UDGothic', sans-serif;
  font-weight: 400; 
  font-size: 1.09375vw;
  letter-spacing: 0.09765625vw;
  line-height: 1.640625vw;
  font-weight: bold;
  color: #FFFBF7;
  background-color: #f16a8d;
  border: .1vw solid #fcdfe7;
  border-radius: .8vw;
  z-index: 3;
  box-shadow: rgba(241, 106, 141, 0.2) 0px 7px 29px 0px;
  cursor: pointer;
  text-transform: uppercase;
  margin-top: -5vw;
  cursor: pointer;
  
} 


.Button {
  display: flex;
  position: relative;
  width: fit-content;
  height: fit-content;
  padding: 1vw;
  font-family: 'BIZ UDPGothic', sans-serif;
  font-weight: 900; 
  font-size: 1.09375vw;
  letter-spacing: 0.09765625vw;
  line-height: 1.640625vw;
  color: #FFFBF7;
  background-color: #f16a8d;
  border: .1vw solid #fcdfe7;
  border-radius: .8vw;
  margin-left: 0vw;
  margin-top: 1vw;
  z-index: 3;
  box-shadow: rgba(241, 106, 141, 0.2) 0px 7px 29px 0px;
  cursor: pointer;
} 

.Button:hover {
  display: flex;
  position: relative;
  width: fit-content;
  height: fit-content;
  padding: 1vw;
  font-family: 'BIZ UDPGothic', sans-serif;
  font-weight: 900; 
  font-size: 1.09375vw;
  letter-spacing: 0.09765625vw;
  line-height: 1.640625vw;
  border-radius: .8vw;
  margin-left: 0vw;
  margin-top: 1vw;
  z-index: 3;
  box-shadow: rgba(241, 106, 141, 0.2) 0px 7px 29px 0px;
  cursor: pointer;
  background-color: #fcdfe7;
  color: #F16A8D;
  border: .1vw solid #F16A8D;

}

.Header {
    display: flex;
    flex-direction: row;
    font-family: 'Cedarville Cursive';
    font-size: 2.6vw;
    letter-spacing: .02vw;
    color: black;
    position: relative;
    z-index: 5;
    margin-left: 5vw;
    z-index: 1;
    margin-top: 2vw;
    padding-top: 3vw;
}

.ImgWrapper {
  display: flex;
  flex-direction: row;
  width: 22vw;
  padding: 1vw;
  justify-content: space-around;
}

.Promo {
  width: 21vw;
}

.Title {
  font-weight: 900;
  font-size: 2.65625vw;
  letter-spacing: 0.01953125vw;
  line-height: 3.9843749999999996vw;
  display: flex;
  flex-direction: row;
  color: #1D1B26;
}

.Body {
  width: fit-content;
  display: flex;
  flex-direction: row;
  justify-content: center;
  text-align: center;
  font-family: 'Roboto', sans-serif;
  font-size: 1.25vw;
  letter-spacing: 0.0390625vw;
  line-height: 1.875vw;
  color: #1D1B26;
}

span {
  font-weight: bold;
  position: inline;
}

.Footer {
  width:100vw;
  color:#1d1b26;
  font-family: "Roboto",sans-serif;
  font-size: .9375vw;
  font-weight: 700;
  letter-spacing: .03125vw;
  line-height: 1.40625vw;
  padding: 1vw;
  text-align: center;
  align-items: center;
  background-image: url(./../../Assets/NavBkg.png);
  border-top: .15vw solid #c2edf4;
  margin-top: -.15vw;
  display: flex;
  flex-direction: column;
  height: fit-content;
  justify-content:flex-start;
  object-fit: cover;
  text-align: start;
}

