.NavContainer {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width: 100vw;
    height: 5vw;
    background-image: url(../../Assets/NavBkg.png);
    background-size: 100vw;
    background-position: cover;
    position: sticky;
    top: 0;
    z-index: 6;
    box-shadow: 0 .625vw 1.875vw rgba(117,174,227,.2);
    background-size:100vw;
}

.Title {
    width: 70vw;
    height: 5vw;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    font-family: 'Cedarville Cursive', cursive;
    font-size: 2.6vw;
    letter-spacing: .02vw;
    color: black;
    margin-left: 2vw;
    position: relative;
    top: 50%;
    transform: translateY(-50%);
}

.SocialLinks{
    width: 5vw;
    height: 4vw;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    margin-right: 2vw;
    z-index: 5;
    position: relative;
    top: 50%;
    transform: translateY(-50%);
}

.SocialLink{
    cursor:pointer;
    display:flex;
    height:4vw;
    justify-content:flex-end;
    padding-left:.5vw;
    position:relative;
    top:50%;
    -webkit-transform:translateY(-50%);
    transform:translateY(-50%);
    width:4vw;
    z-index:5;
}

.Squeeze {
    display: flex;
    flex-direction: row;
    width: 5vw;
    height: 4vw;
    justify-content: flex-end;
}

span {
    all: inherit;
}

button, a {
    all: inherit;
}

